.hover-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateY(0%);
    background-color: rgba(255, 255, 255, 0.5);
    transition: transform 0.5s ease-in-out;
    
  }
.hover-overlay:hover{
    transform: translateY(75%);
    
}
